eria. ")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"2.1.9.2.2"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('h6',[_vm._v(" Procedimiento para efectuar registro de los libros de comercio registrables en medios electrónicos:")]),_c('p',{staticClass:"mt-3"},[_vm._v("De conformidad con lo establecido en la ley, los comerciantes que quieran llevar sus libros de comercio registrables, en medios electrónicos, deberán solicitarlo de manera expresa ante la Cámara de Comercio correspondiente a su domicilio, indicando una dirección de correo electrónico a la cual se le puedan remitir las inscripciones efectuadas y aceptando los términos y condiciones por ella establecidas para el efecto. Una vez recibida la solicitud de inscripción de los libros registrables, por parte del interesado, la Cámara de Comercio correspondiente deberá efectuar una inscripción por cada uno de los libros en el Libro respectivo del registro de que se trate, debiendo devolver al solicitante el archivo electrónico al correo electrónico que tenga reportado el comerciante o inscrito. Para ello, la Cámara de Comercio deberá firmarlo digital o electrónicamente, dejando constancia electrónica de la fecha y la hora en que fue enviado o remitido el archivo, por cualquier medio tecnológico disponible. De conformidad con lo establecido en las normas que rigen la materia, la constancia electrónica expedida por la Cámara de Comercio correspondiente, deberá tener la siguiente información: – Cámara de Comercio receptora. – Fecha de presentación del libro para registro. – Fecha de inscripción. – Número de inscripción. – Identificación del comerciante o persona obligada a registrar. – Nombre del libro, y – Uso al que se destina. A su vez, si a la fecha de la solicitud de inscripción del libro registrable por medios electrónicos, el libro físico que lo antecede posee hojas que no hubieren sido empleadas, deberán ser anuladas, de acuerdo con lo previsto en las normas que rigen la materia.")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"2.1.9.2.3"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mt-3"},[_vm._v("Conformación de los libros registrados en medios electrónicos Efectuada la inscripción del libro de actas de juntas de socios o accionistas en medios electrónicos, el comerciante o inscrito tendrá derecho a remitir a la Cámara de Comercio, por el término de un (1) año, los archivos electrónicos en donde consten las actas de dicho órgano, los cuales deberán ser firmados digital o electrónicamente por quienes actuaron como presidente y secretario de la reunión. La solicitud de asentar el archivo electrónico contentivo del acta, en el libro correspondiente, deberá estar suscrita digital o electrónicamente por el representante legal, presidente o secretario. Efectuada la inscripción del libro de socios o accionistas en medios electrónicos, el comerciante o inscrito tendrá derecho a remitir a la Cámara de Comercio, por el término de un (1) año, archivos electrónicos destinados a ese libro, los cuales deberán ser firmados digital o electrónicamente por el representante legal. Es responsabilidad de cada comerciante la provisión de las firmas digitales o electrónicas y, estampas cronológicas necesarias. Una vez recibidos los archivos electrónicos, la Cámara de Comercio correspondiente deberá devolver al solicitante el archivo electrónico al correo electrónico que tenga reportado el comerciante o inscrito. Para ello, la Cámara de Comercio deberá firmarlo digital o electrónicamente, dejando constancia electrónica de la fecha y la hora en que fue enviado o remitido el archivo por cualquier medio tecnológico disponible.")])])])])]),_c('h6',[_vm._v("Libros auxiliares:")]),_c('p',{staticClass:"mt-3"},[_vm._v("Son los libros que reflejan de forma ordenada las operaciones realizadas por la empresa, afectando los débitos y créditos de una o varias cuentas y subcuentas, con el fin de generar el saldo en una fecha determinada.")]),_c('p',{staticClass:"mt-3"},[_vm._v("Entre los libros auxiliares se encuentran: libro auxiliar de bancos, caja, IVA, inventarios, ingresos ordinarios, etc.")]),_vm._m(10),_c('p'),_c('hr'),_vm._m(11),_c('hr',{staticClass:"mt-5 mb-4"}),_c('h6',{staticClass:"mt-5"},[_vm._v("Sistema de gestión documental emitido por el ente regulador")]),_c('p',{staticClass:"mt-3"},[_vm._v("El Estado colombiano busca, mediante la Resolución 8934 de febrero de 2014, implementar el sistema de gestión documental, con el objetivo de mantener organizados los archivos en el sector privado, enfocada básicamente en la organización de:")]),_vm._m(12),_c('p'),_c('hr')],1),_c('Muestras')],1)}
